import { Link } from 'react-router-dom';
import ReactPlayer from 'react-player';
import objects from '../images/team/objects.png';
import card from '../images/team/card.png';
import People from './People';
import Hint from './Hint';
import { hintData } from '../utils/constants';
import React, { useEffect } from 'react';

export default function Team () {
    
    useEffect(() => {
        window.scrollTo(0, 0)
    }, [])

    return (
        <>
        <section className="team">
        <div className="main__info">
                <h2 className="main__title"> </h2>
                <div className="main__box">
                    <Link className="main__link" to="/"></Link>
                    <p className="main__text">/ </p>
                </div>
            </div>
        </section>
        <div className="paragraph">
                <h2 className="paragraph__title"> ?</h2>
                <p className="paragraph__text">   -,  -   ,   ,           .</p>
        </div>
        <div className="team__box">
            <ReactPlayer className="team__video" url='https://www.youtube.com/embed/bLwQnK6xplI' />
        </div>
        <div className="payment-procedure">
        <p className="payment-procedure__text">DELUXE HOUSE              ,            .</p>
        <p className="payment-procedure__text">      ,      .       ,      .</p>
        <p className="payment-procedure__text">        -   ,  ,         !</p>
        </div>
        <section className='years'>
            <div className='years__box'>
                <img className="years__img" src={card} alt=""/>
                <div className='years__container'>
                    <h2 className="years__title"><span className="years__title_big">10</span> </h2>
                    <p className="years__title_smoll"> !</p>
                    <p className="years__text">           2011 .           200 .</p>
                    <p className="years__text">    -,      ,   , ,  , , SPA, , , , , -  .</p>
                    <p className="years__text">   ,         ,  ,     .</p>
                </div>
            </div>
        </section>
        <People/>
        <section className="hint">
            <h2 className="title__smoll">   </h2>
            <div className="line"></div>
                {hintData.map(({id, title, content }) => (
                <Hint key={id} title={title} content={content} />
                ))}
            <div className="button__box">
                    <Link className="link-black" to="/articles">  </Link>
            </div>
        </section>
        <div className="paragraph">
                <h2 className="paragraph__title">  ?</h2>
                <p className="paragraph__text">        :        .</p>
        </div>
        <div className="team__box">
            <img className="team__img" src={objects} alt=""/>
        </div>
        </>
)
}